import React from 'react';
import Section from "../../Atomics/Section";
import MyCourseCard from "../../Atomics/MyCourseCard";
import RankTable from "../../Atomics/RankTable";
import CourseOverview from "./CourseOverview";

const Home = () => {

    return (
        <>
            <Section title={'Mein Kurs'}>
                <MyCourseCard title={'Platzhalter'} progress={0.2}/>
            </Section>
            <Section title={'Kurse'}>
                <CourseOverview />
            </Section>
            <Section title={'Rangliste'}>
                <RankTable />
            </Section>
        </>
    )
};

export default Home;