import React from 'react';
import CourseCarousel from "../../Atomics/CourseCarousel";
import CourseCard from "../../Atomics/CourseCard";
import {useGetData} from "../../Providers/CourseProvider";

const CourseOverview = () => {
    const courses = useGetData('Courses');

    const constructCourseCards = () => {
        const temp = [];

        for(let key in courses) {
            const {title, description, symbol, id} = courses[key];
            temp.push(
                <CourseCard key={id ?? 0} title={title} to={`/course/${id}`} symbol={symbol}>
                    {description}
                </CourseCard>
            );
        }

        return temp;
    };

    return (
        <CourseCarousel>
            {constructCourseCards()}
        </CourseCarousel>
    )
};

export default CourseOverview;